import React from 'react';

export default class ButtonMob extends React.Component {

    constructor()
    {
        super();
        this.state={
            visNav:'flex'
        }
    }

    /*Fun√ß√£o toggle*/
    clickMe()
    {
        if(this.state.visNav === 'flex'){
            this.setState({
                visNav:'none'
            });
        }else{
            this.setState({
                visNav:'flex'
            });
        }
        document.querySelector(".nav").style.display=this.state.visNav;
    }

    render(){
        return(
            <div className="buttonMob" onClick={this.clickMe.bind(this)}>
                <img src="https://i.ibb.co/7kDm0nH/botao.png" alt="Bot√£o Mobile" height="50px"/>
            </div>
        );
    }
}