import React from 'react';

export default class Logo extends React.Component {

    render(){
        return(
            <div className="logo">
                <picture>
                    <source media="(max-width: 768px)" srcSet="https://i.ibb.co/LnL0QKZ/logo.png"/>
                    <source media="(min-width: 769px)" srcSet="https://i.ibb.co/LnL0QKZ/logo.png"/>
                    <img src="https://i.ibb.co/LnL0QKZ/logo.png" alt="Logo Sitio" width="70px"/>
                </picture>
            </div>
        );
    }
}